import Vue from 'vue';
import Router from 'vue-router';
import HelloWorld from '../components/HelloWorld';
import Search from '../components/SearchMatches';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/home',
      component: HelloWorld,
      children: [
        { path: '', component: Search },
      ],
    },
  ],
});



